
import Vue from "vue";
import { Prop, Component } from "vue-property-decorator";

@Component
export default class Btn extends Vue {
  @Prop({
    required: true,
    default: "",
  })
  public to!: string;
  @Prop({
    required: true,
    default: "",
  })
  public text!: string;

  private data() {
    return {
      link: this.$props.to,
      caption: this.$props.text,
    };
  }
}
